section{
    display: grid;
    grid-template-columns: 70% 30%;
}

.photos{
    background: var(--main-color);
}

.photos h1{
    font-size: 3em;
    font-weight: bold;
    padding: 20px 40px;
    box-sizing: border-box;
}

.photos ul:nth-of-type(1){
    height: 460px;
    display: grid;
    justify-content: center;
    grid-gap: 20px;
    padding: 20px;
    grid-template-columns: repeat(2, 150px);
    overflow: scroll;
    box-sizing: border-box;
}

.photos ul:nth-of-type(1) li img{
    width: 150px;
    height: 200px;
    border-radius: 7px;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.photos ul:nth-of-type(1) li img:hover{
    opacity: 0.9;
    transform: scale(1.2)
}

.storyLine{
    display: grid;
    justify-items: flex-start;
    padding: 40px;
    box-sizing: border-box;
    color: #50606F;
}

.storyLine h1{
    font-size: 3em;
}

.storyLine ul:nth-of-type(1){
    display: flex;
    align-items: center;
}

.storyLine ul:nth-of-type(1) li{
    padding: 10px 20px;
    border-radius: 7px;
    background: rgba(0, 0, 0, 0.05);
    margin: 5px;
}

.storyLine ul:nth-of-type(2){
    display: grid;
    grid-gap: 20px;
}

::-webkit-scrollbar {
    width: 15px;
  }
  
  /* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 1); 
    border-radius: 10px;
}
   
  /* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.5); 
    border-radius: 10px;
}
  
  /* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.8); 
}

@media screen and (max-width: 768px){
    section{
        grid-template-columns: 100%;
    }
}