.img64, .button img{
    width: 64px;
    height: 64px;
}

.img32{
    width: 32px;
    height: 32px;
}

ul{
    list-style: none;
}

.hover, .button{
    cursor: pointer;
}

.hover:hover, .button:hover{
    opacity: 0.5;
}

.button{
    display: grid;
    grid-template-columns: 60px auto;
    grid-gap: 30px;
    justify-content: center;
    align-items: center;
    padding: 5px 10px;
    border-radius: 10px;
    box-shadow: 10px 10px 20px rgba(0, 0,0,0.8)
}

.button img{
    padding: 20px;
    background: rgba(0, 0,0,0.1);
    box-sizing: border-box;
    border-radius: 32px;
}

.button p{
    font-size: 1.2em;
    font-weight: bold;
}