header{
    width: 100%;
    display: grid;
    grid-template-columns: 70% 30%;
    grid-template-rows: 100px 500px;
    grid-gap: 0px;
    background-image: url('../images/strangerthings-banner.png');
    background-position: center;
    background-size: auto 100%;
    background-color: black;
    background-repeat: no-repeat;
    box-sizing: border-box;
    overflow: hidden;
}

.headerBasicInfo{
    box-sizing: border-box;
    padding: 20px;
}

.headerBasicInfo div{
    margin-left: 20px;
}

.headerBasicInfo ul,.headerBasicInfo{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.headerBasicInfo li::after{
    content: '|';
}

.headerBasicInfo p, .headerBasicInfo li{
    color: white;
}

.headerBasicInfo p{
    font-weight: bold;
    font-size: 1.5em;
}

.headerNavBar{
    display: grid;
    align-content: center;
}

.headerNavBar ul{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    align-items: center;
}

.headerNavBar ul li{
    color: var(--main-color);
    font-weight: bold;
    text-align: center;
    padding: 5px;
    box-sizing: border-box;
    border-radius: 10px;
}

.headerNavBar ul li:nth-of-type(4){
    color: white;
    background: rgba(0,0,0,0.4);
} 

.headerWatchTrailer{
    display: grid;
    justify-content: left;
    align-items: center;
    padding-left: 100px;
    box-sizing: border-box;
}

.headerWatchTrailer .button{
    background: #FDFDFD;
}

.headerWatchTrailer .button p{
    color: var(--title-color);
}

.headerInfo{
    display: grid;
    grid-template-columns: 100%;
    justify-content: center;
    box-sizing: border-box;
}

.episodeGuide{
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: center;
    grid-gap: 40px;
}

.episodeGuide div{
    width: 100%;
}

.episodeGuide div p{
    font-size: 4em;
    font-weight: bold;
    color: white;
    display: inline;
}

.episodeGuide div span{
    font-size: 0.3em;
    font-weight: normal;
}

.episodeGuide div span::before{
    content: '';
    background: url('../images/star.png');
    background-size: cover;
    width: 32px;
    height: 32px;
    position: absolute;
    display: inline-block;
}

.episodeGuide form p{
    color: white;
    padding: 10px 0;
    box-sizing: border-box;
}

.episodeGuide form select{
    outline: none;
    border: none;
    color: var(--main-color);
    padding: 5px 6px;
    border-radius: 5px;
    background: rgba(0,0,0,0.4);
}

.serieInfo{
    padding: 20px;
    box-sizing: border-box;
}

.serieInfo p{
    color: white;
    padding: 5px;
    font-size: 0.9em;
}

.addToWatchlist{
    padding: 20px;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-items: center;
    align-items: center;
}

.addToWatchlist .button{
    background: var(--main-color);
    justify-self: left;
    width: auto;
    grid-column-start: 1;
    grid-column-end: 3;
}

.addToWatchlist .button p{
    color: black;
}

.addToWatchlist div p{
    color: white;
}

.addToWatchlist div p span{
    font-weight: 300;
}
.addToWatchlist div:nth-of-type(3){
    display: flex;
}

.addToWatchlist img{
    margin-right: 10px;
    padding: 10px;
    background: black;
    border-radius: 10px;
}

.youtubeTrailer{
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.4);
    display: grid;
    grid-template-rows: 10vh 90vh;
    justify-content: center;
    align-content: center;
}

.youtubeTrailer iframe{
    border: 10px solid white;
    border-radius: 10px;
    width: 700px;
    height: 394px;
}

.youtubeTrailer img{
    justify-self: left;
    align-self: center;
}

@media screen and (max-width: 768px){
    header{
        grid-template-columns: 100%;
        grid-template-rows: 70px 70px 250px auto;
        overflow: auto;
    }

    .headerBasicInfo{
        background: rgba(70,70,70,0.7);
        grid-row-start: 2;
        grid-row-end: 3;
    }

    .headerWatchTrailer{
        justify-content: center;
        align-items: center;
        padding: 0;
    }

    .youtubeTrailer iframe{
        width: 425px;
        height: 239px;
    }

    .addToWatchlist{
        grid-gap: 20px;
    }
}

@media screen and (max-width: 425px){
    .headerBasicInfo img{
        width: 32px;
        height: 32px;
    }

    .headerBasicInfo p{
        font-size: 1em;
    }

    .headerBasicInfo ul li{
        font-size: 0.8em;
        text-align: center;
    }

    .headerBasicInfo ul li::after{
        content: ''
    }

    .youtubeTrailer iframe{
        width: 300px;
        height: 169px;
    }
}